import React, { useEffect } from "react";
import { peopleSelector, personSelector } from "../../selectors";
import { connect } from "react-redux";
import { fetchPeople } from "../../ac";

function PersonPage({ person, match, fetchPeople }){

    useEffect(() => {
      if (!person) {
        fetchPeople(match.params.id);
      }
    }, [person]);

    return person ? <React.Fragment>
      <p><b>Name: </b> {person.name}</p>
      <p><b>Gender: </b>{person.gender}</p>
      <p><b>Culture: </b>{person.culture}</p>
      <p><b>Titles: </b>{person.titles.join(", ")}</p>
    </React.Fragment> : <div/>
    // return <p><NavLink to={`/people/${person.id}`}>{person.name}</NavLink></p>;
  }

export default connect(
  (state, ownProps) => ({
    person: personSelector(state, ownProps)
  }),
  { fetchPeople }
)(PersonPage);
